<template>
  <div name="activity">
    <expandable-section-std title="Activity" ref="expandActivity" :closedOnStart="!disabledStatusMonitoring">
      <div class="data-frame">

        <div class="row">
          <div class="col-6 padding-b0" >
            <label class="slds-form-element__label padding-b0" ><abbr class="slds-required" title="required">* </abbr>Activity</label>
          </div>
  
          <div class="col-3 padding-b0">
            <label class="slds-form-element__label padding-b0" ><abbr class="slds-required" title="required">* </abbr>Period/Frequency</label>
          </div>
          <div class="col-3 padding-b0">
            <label class="slds-form-element__label padding-b0" ><abbr class="slds-required" title="required">* </abbr>Action By</label>
          </div>
          <div class="col-sm-12">
            <div v-for="(activitySet, index) in emocActivityOp" class="row" :key="index">

              <div class="col-6">
                <div class="row">
                  <div class="col-11 padding-t0" :class="{'col-12': disabledSectionActivity}" >
                  
                    <input-text-std
                      v-model="activitySet.Name"
                      v-validate="(!activitySet.flgDelete) ? 'required' : ''" 
                      data-vv-as="Activity"
                      data-vv-value-path="$data.$_value"
                      :name="'Name' + index"
                      label=""
                      :error="errors.has('Name' + index)"
                      :required="true"
                      :disabled="activitySet.flgDelete || disabledSectionActivity || !editEnable"
                      :maxlength="80"
                    >
                      <template #error>
                        <span>{{ errors.first('Name' + index) }}</span>
                      </template>
                    </input-text-std>
                  </div>

                  <div v-if="!disabledSectionActivity && editEnable" class="col-1 delete-mapping-optional padding-t0" :class="{'delete-mapping-optional-disabled': activitySet.flgDelete}">
                    <i class="fa fa-trash-o icon-delete-mapping-optional" @click="clickDelete(activitySet.rowId)" aria-hidden="true"></i>
                  </div>
                </div>
              </div>

              <div class="col-3">
                <div class="row">
                  <div class="col-12 padding-t0">
                    <combobox-std
                      v-model="activitySet.Period_frequency__c"
                      v-validate="!activitySet.flgDelete ? 'required' : ''"
                      data-vv-as="Potential consequence"
                      data-vv-value-path="textInputValue"
                      :name="'Period_frequency__c' + index"
                      :error="errors.has('Period_frequency__c' + index)"
                      :options="periodFrequencyList"
                      label=""
                      :disabled="activitySet.flgDelete || disabledSectionActivity || !editEnable"
                      :required="true"
                    >
                      <template #error>
                        <span>{{ errors.first('Period_frequency__c' + index) }}</span>
                      </template>
                    </combobox-std>
                  </div>
                </div>
              </div>
              <div class="col-3">
                <div class="row">
                  <div class="col-12 padding-t0">
                    <lookups-std v-model="activitySet.Action_by__c"
                                 v-validate="!activitySet.flgDelete ? 'required' : ''"
                                 data-vv-as="Action by"
                                 data-vv-value-path="textInputValue"
                                 label=""
                                 placeholder="Search..."
                                 :options="optionsLookupUser(index, activitySet)"
                                 :required="true"
                                 :name="'Action_by__c' + index"
                                 :error="errors.has('Action_by__c' + index)"
                                 @search="filterLookupUser('lookupUser' + index, arguments[0])"
                                 @setDataRow="setDataRow(index, arguments[0])"
                                 icon="people"
                                 :disabled="activitySet.flgDelete || disabledSectionActivity || !editEnable"
                                 loading>
                      <template #error>
                        <span>{{ errors.first('Action_by__c' + index) }}</span>
                      </template>
                    </lookups-std>
                  </div>
                </div>
              </div>

            </div>
            <div class="row" v-if="!disabledSectionActivity && editEnable">
              <div class="col-sm-6">
                <button class="slds-button slds-button_neutral" @click="clickAdd"><i class="fa fa-plus" aria-hidden="true"></i>&nbsp;&nbsp;Add</button>
              </div>
            </div>
          </div>
        </div>

      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'activity',
  props: [
    'emoc',
    'disabledSectionActivity',
    'editEnable',
    'disabledStatusMonitoring'
  ],
  data () {
    return {
      lookupUserType: 'lookupUser'
    }
  },
  computed: {
    periodFrequencyList () { return this.$store.state.periodFrequencyList },
    emocActivity () { return this.$store.state.emocActivity },
    lookupUser () { return this.$store.state.lookupUser },
    emocActivityOp () {
      return this.emocActivity.map((df, idx) => {
        df['rowId'] = idx
        return df
      })
    }
  },
  methods: {
    filterLookupUser (type, searchParam) {
      this.$store.dispatch('receiveLookupUser', {type, sendParam: searchParam})
    },
    optionsLookupUser (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser('lookupUser' + rowIndex, dataRow.Action_by__r)
        return []
      }
    },
    setDataRow (rowIndex, dataRow) {
      if (typeof dataRow.Action_by__c === 'undefined') {
        this.filterLookupUser('lookupUser' + rowIndex, '')
      }
    },
    clickAdd () {
      this.$validator.validateAll()
        .then((result) => {
          if (result) {
            let lastIndex = this.emocActivity.length
            this.emocActivity.push({
              rowId: lastIndex,
              Id: null,
              E_MOC_Application__c: this.emoc.Id,
              Name: '',
              Period_frequency__c: '',
              Action_by__c: '',
              Action_by__r: '',
              Frequency_days__c: '',
              flgDelete: false,
              Acknowledge__c: false,
              Acknowledge_by__c: '',
              Acknowledge_by__r: ''
            })
          }
        })
    },
    clickDelete (rowId) {
      let setEmocActivity = JSON.parse(JSON.stringify(this.emocActivity))
      setEmocActivity[rowId]['flgDelete'] = !setEmocActivity[rowId]['flgDelete']
      this.$nextTick(() => {
        this.$store.commit('setEmocActivity', setEmocActivity)
      })
    }
  }
}
</script>
<style scoped>
  .delete-mapping-optional {
    font-size: large;
    color: #0176d3;
  }
  .delete-mapping-optional-disabled {
    color: rgb(234, 0, 30);
  }
  .delete-mapping-optional
    .icon-delete-mapping-optional {
      cursor: pointer;
    }
/*  .slds-input-has-icon_right .slds-input {
    text-decoration: line-through;
  }*/
  .padding-b0{
    padding-bottom: 0px;
  }
  .padding-t0{
    padding-top: 0px;
  }
</style>


