<template>
  <div name="extend-prevent">
    <expandable-section-std title="Require to extend to prevent overdue during return to normal?" ref="expandRequireExtendPreventoverdue" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-6">
            <div class="row">
              <div class="col-12">
                <checkbox-std
                  v-model="emocTemporaryHistory2.Yes2__c"
                  label="Extend"
                  :inline="true"
                  :disabled="!editEnable"
                />
              </div>
        <!--       <div class="col-12" v-if="emocTemporaryHistory2.Yes2__c">
                <textarea-std
                  v-model="emocTemporaryHistory2.Remark__c"
                  data-vv-as="Reason for Extension"
                  data-vv-value-path="inputValue"
                  name="Remark__c"
                  label="Reason for Extension"
                  :error="errors.has('Remark__c')"
                  disabled
                  :rows="1"
                >
                </textarea-std>
              </div> -->
      <!--         <div class="col-6" v-if="emocTemporaryHistory2.Yes2__c">
                <label class="slds-form-element__label">
                 New Require End Date
                </label>
                <v-menu v-model="showPickerStartDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emocTemporaryHistory2.Date__c"
                      name="Date__c"
                      @blur="dateStart = parseDate(emocTemporaryHistory2.Date__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      disabled
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateStart"
                    @change="showPickerStartDate = false"
                    :min="minCalendar"
                  ></v-date-picker>
                </v-menu>
              </div> -->
            </div>
          </div>
          <div class="col-6">
            <div class="row">
              <div class="col-12">
                <checkbox-std
                  v-model="emoc.Not_Extend_Require_to_extend_to_prevent__c"
                  label="Not Extend"
                  name="No2__c"
                  :inline="true"
                  :disabled="!editEnable"
                />
              </div>
              <div class="col-12" v-if="emoc.Not_Extend_Require_to_extend_to_prevent__c">
                <textarea-std
                  v-model="emoc.Remark__c"
                  data-vv-as="Remark"
                  data-vv-value-path="inputValue"
                  name="Remark__c"
                  label="Remark"
                  :error="errors.has('Remark__c')"
                  :rows="1"
                  :disabled="!editEnable"
                >
                </textarea-std>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-show="checkRequireExtendPreventoverdue" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          {{'The required to extend to prevent overdue during return to normal is required'}}
        </div>
      </div>

    </expandable-section-std>

    <modal-std size="large" ref="refModalRequireExtendPrevent" @close="afterClosePopup" id="requireExtendPrevent">
      <template #header>
        <div style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">Require to extend to prevent overdue during return to normal</span></div>
      </template>
      <template #content>
        <require-extend-prevent-popup   :emocTemporaryHistory2="emocTemporaryHistory2"
                                        :labelTypeCheckbox="labelTypeCheckbox"
                                        :emoc="emoc"
                                        ref="refRequireExtendPrevent"/>
      </template>
      <template #footer>
        <div style="text-align: right;">
          <button class="slds-button slds-button_neutral" @click="callBackRequireExtendPrevent">Cancel</button>
          <button v-if="btnComfirmDisable2" class="slds-button slds-button_brand" @click="callSave">Comfirm</button>
          <button v-else class="slds-button slds-button_brand" disabled @click="callSave">Comfirm</button>
        </div>
      </template>
    </modal-std>

    <modal-after-save id="afterSaveRequireExtendPrevent"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveRequireExtendPrevent"/>

  </div>
</template>
<script>

import requireExtendPreventPopup from './RequireExtendPreventPopup.vue'
import moment from 'moment'

export default {
  name: 'extend-prevent',
  props: [
    'emoc',
    'recId',
    'checkRequireExtendPreventoverdue',
    'disabledNextStage',
    'editEnable',
    'clickSaveMain'
  ],
  data () {
    return {
      showPickerStartDate: false,
      dateStart: '',
      minCalendar: moment().format('YYYY-MM-DD'),
      labelTypeCheckbox: ''
    }
  },
  computed: {
    btnComfirmDisable2 () { return this.$store.state.btnComfirmDisable2 },
    emocTemporaryHistory2 () { return this.$store.state.emocTemporaryHistory2 },
    setRuleExtend () {
      return (this.emocTemporaryHistory2.Yes2__c === true) ? 'required' : ''
    },
    setRuleDateStart () {
      return (this.emocTemporaryHistory2.Yes2__c === true) ? 'required|date_format:dd/MM/yyyy' : ''
    }
  },
  watch: {
    'emoc.Not_Extend_Require_to_extend_to_prevent__c' (newValue) {
      if (newValue) {
        this.emocTemporaryHistory2.Yes2__c = false
        if (newValue === true) {
          this.labelTypeCheckbox = 'Not Extend'
        }
      }
    },
    'emocTemporaryHistory2.Yes2__c' (newValue) {
      if (newValue) {
        this.emoc.Not_Extend_Require_to_extend_to_prevent__c = false
        this.emoc.Remark__c = ''
        if (newValue === true) {
          this.showPopupRequireExtendPrevent()
          this.labelTypeCheckbox = 'Extend'
        }
      }
    },
    dateStart (val) {
      this.emocTemporaryHistory2.Date__c = this.formatDate(this.dateStart)
    }
  },
  methods: {
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    showPopupRequireExtendPrevent () {
      this.$refs.refModalRequireExtendPrevent.onOpen()
    },
    afterClosePopup () {
    },
    callBackRequireExtendPrevent () {
      this.$nextTick(() => {
        this.$refs.refModalRequireExtendPrevent.onClose()
      })
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSaveRequireExtendPrevent.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    callSave () {
      this.emocTemporaryHistory2.Type__c = 'Require to extend to prevent overdue during return to normal'
      this.callBackRequireExtendPrevent()
      if (this.emocTemporaryHistory2.Yes2__c) {
        this.clickSaveMain()
      }
      // this.$store.commit('setRequireExtendPreventDisable', true)
    }
  },
  components: {
    requireExtendPreventPopup
  }
}
</script>


