<template>
  <div name="monitoring-program">
    <expandable-section-std title="Monitoring Program Requirement" ref="expandMonitoringProgram" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-6">
            <div class="row">
              <div class="col-6">
                <checkbox-std
                  v-model="emoc.Yes_Monitoring_Program_Requirement__c"
                  label="Yes"
                  :inline="true"
                  :disabled="!editEnable || disabledStatusMonitoring"
                />
              </div>
            </div>
            <div  v-if="emoc.Yes_Monitoring_Program_Requirement__c" class="row">
              <div class="col-md-6">
                <label class="slds-form-element__label">
                  <abbr class="slds-required" title="required">*</abbr>Required Start date
                </label>
                <v-menu v-model="showPickerStartDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emoc.Start_Date_Monitoring_Program__c"
                      v-validate="'required|date_format:dd/MM/yyyy'"
                      data-vv-as="Start date"
                      data-vv-value-path="inputValue"
                      name="Start_Date_Monitoring_Program__c"
                      :error="errors.has('Start_Date_Monitoring_Program__c')"
                      @blur="dateStart = parseDate(emoc.Start_Date_Monitoring_Program__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      :disabled="!editEnable"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateStart"
                    @change="showPickerStartDate = false"
                    :min="minCalendar"
                  ></v-date-picker>
                </v-menu>
                <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Start_Date_Monitoring_Program__c') }}</span></div>
              </div>
              <div class="col-md-6">
                <label class="slds-form-element__label">
                  <abbr class="slds-required" title="required">*</abbr>Required End date
                </label>
                <v-menu v-model="showPickerEndDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emoc.End_Date_Monitoring_Program__c"
                      v-validate="'required|date_format:dd/MM/yyyy|after:' + checkDateAfter"
                      data-vv-as="End date"
                      data-vv-value-path="inputValue"
                      name="End_Date_Monitoring_Program__c"
                      :error="errors.has('End_Date_Monitoring_Program__c')"
                      @blur="dateEnd = parseDate(emoc.End_Date_Monitoring_Program__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      :disabled="!editEnable"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateEnd"
                    @change="showPickerEndDate = false"
                    :min="minCalendar"
                  ></v-date-picker>
                </v-menu>
                <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('End_Date_Monitoring_Program__c') }}</span></div>
              </div>
            </div>
          </div>
          <div class="col-6">
            <div class="row">
              <div class="col-6">
                <checkbox-std
                  v-model="emoc.No_Monitoring_Program_Requirement__c"
                  label="No"
                  name="No_Monitoring_Program_Requirement__c"
                  :inline="true"
                  :disabled="!editEnable || disabledStatusMonitoring"
                />
              </div>
            </div>
            <div v-if="emoc.No_Monitoring_Program_Requirement__c" class="row">

              <div class="col-md-12">
                <textarea-std
                  v-model="emoc.Remark_Monitoring_Program__c"
                  v-validate="'required'"
                  data-vv-as="Remark"
                  data-vv-value-path="inputValue"
                  name="Remark_Monitoring_Program__c"
                  label="Reason for not using monitoring program."
                  :error="errors.has('Remark_Monitoring_Program__c')"
                  :required="true"
                  :rows="1"
                  :disabled="!editEnable"
                >
                  <template #error>
                    <span>{{ errors.first('Remark_Monitoring_Program__c') }}</span>
                  </template>
                </textarea-std>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-show="checkRequiredMonitoringProgram" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          {{'The monitoring program requirement field is required.'}}
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
import moment from 'moment'

export default {
  name: 'monitoring-program',
  props: [
    'emoc',
    'recId',
    'checkRequiredMonitoringProgram',
    'disabledStatusMonitoring',
    'editEnable'
  ],
  data () {
    return {
      showPickerStartDate: false,
      showPickerEndDate: false,
      dateStart: '',
      dateEnd: '',
      nameRules: [
        v => !!v || 'Name is required'
      ],
      minCalendar: moment().format('YYYY-MM-DD')
    }
  },
  computed: {
    checkDateAfter () {
      return moment(this.emoc.Start_Date_Monitoring_Program__c, 'DD/MM/YYYY').add(-1, 'days').format('DD/MM/YYYY')
    }
  },
  watch: {
    dateStart (val) {
      this.emoc.Start_Date_Monitoring_Program__c = this.formatDate(this.dateStart)
    },
    dateEnd (val) {
      this.emoc.End_Date_Monitoring_Program__c = this.formatDate(this.dateEnd)
      let showRequireToExtend = moment(this.emoc.End_Date_Monitoring_Program__c, 'DD/MM/YYYY').add(-30, 'days').format('DD/MM/YYYY')
      this.emoc.Show_Require_to_Extend__c = this.parseDate(showRequireToExtend)
    },
    'emoc.No_Monitoring_Program_Requirement__c' (newValue) {
      if (newValue) {
        this.emoc.Yes_Monitoring_Program_Requirement__c = false
      }
    },
    'emoc.Yes_Monitoring_Program_Requirement__c' (newValue) {
      if (newValue) {
        this.emoc.No_Monitoring_Program_Requirement__c = false
        // let paramSearch = {
        //   emocApplicationId: this.recId
        // }
        // if (this.editEnable) {
        //   this.$store.dispatch('receiveEmocActivity', paramSearch)
        // }
      }
    }
  },
  methods: {
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    }
  }
}
</script>


