import Vue from 'vue'
import Vuex from 'vuex'
import createEmocApi from '../api/createEmoc'
import moment from 'moment'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    lookupUser: {},
    lookupEmocNumber: [],
    lookupNotification: [],
    emoc: {
      Id: null,
      Status_Common_Workflow__c: 'Technical Review',
      Yes_Monitoring_Program_Requirement__c: false,
      No_Monitoring_Program_Requirement__c: false,
      Start_Date_Monitoring_Program__c: '',
      End_Date_Monitoring_Program__c: '',
      Remark_Monitoring_Program__c: '',
      Show_Require_to_Extend__c: '',
      Extend_Require_to_extend__c: false,
      Not_Extend_Require_to_extend__c: false,
      Reason_for_Extension_temporary_MOC__c: '',
      Yes_Require_to_demolish__c: false,
      No_Require_to_demolish__c: false,
      Reason_for_Extension_return_to_normal__c: '',
      Comfirm: false, // สร้างเอง
      EMOC_Number_for_permanent_MOC__c: '',
      Notification_for_Permanent__c: '',
      Requestor_Name__c: '',
      Department_Resp__c: '',
      Requestor_ID__c: '',
      checkFirstConfirmDemolish: true,
      Show_Correct_the_problem_section__c: false,
      End_date__Old: '', // สร้างเอง
      End_date__c: '',
      newEndDateTempoHis: '', // สร้างเอง
      User_Login: '', // สร้างเอง
      Return_to_Normal__c: false,
      Action_by_return_to_normal__c: '',
      flgShowConfirmeNoRequireDemolish: false, // สร้างเอง
      Not_Extend_Require_to_extend_to_prevent__c: false,
      Extend_Require_to_extend_to_prevent__c: false,
      Remark__c: '',
      EMOCNumberForPermanentMocLink: '',
      EMOCNumberForPermanentMocName: ''
    },
    periodFrequencyList: [],
    emocActivity: [],
    emocActivityLength: 0,
    editEnable: false,
    btnComfirmDisable: false,
    btnComfirmDisable2: false,
    btnComfirmDisable3: false,
    monitoringResult: [],
    monitoringResultLength: 0,
    emocTemporaryHistory: {
      E_MOC_Application__c: null,
      Id: null,
      Yes__c: false,
      No__c: false,
      Remark__c: '',
      Date__c: '',
      Comfirm: false, // สร้างเอง
      Type__c: '',
      showSectionCorrectProblem: false
    },
    emocTemporaryHistory2: {
      E_MOC_Application__c: null,
      Id: null,
      Yes2__c: false,
      No2__c: false,
      Remark__c: '',
      Date__c: '',
      Comfirm: false, // สร้างเอง
      Type__c: '',
      showSectionCorrectProblem: false
    },
    correctProblemDisable: false
  },
  getters: {
    getLookupUser: (state) => {
      return state.lookupUser
    }
  },
  mutations: {
    setEditEnable: (state, data) => {
      state.editEnable = data
    },
    setLookupUser: (state, {type, data}) => {
      let temp = state.lookupUser
      temp[type] = data
      state.lookupUser = JSON.parse(JSON.stringify(temp))
    },
    setLookupEmocNumber: (state, data) => {
      state.lookupEmocNumber = data
    },
    setLookupNotification: (state, data) => {
      state.lookupNotification = data
    },
    setEmocApplication: (state, data) => {
      if (data.Show_Require_to_Extend__c === '' || typeof data.Show_Require_to_Extend__c === 'undefined') {
        let showRequireToExtend = moment(data.End_Date_Monitoring_Program__c, 'DD/MM/YYYY').add(-30, 'days').format('DD/MM/YYYY')
        data.Show_Require_to_Extend__c = moment(showRequireToExtend, 'DD/MM/YYYY').format('YYYY-MM-DD')
      }
      let setDefaultField = data
      state.emoc = Object.assign(state.emoc, setDefaultField)
    },
    setPeriodFrequencyList: (state, data) => {
      state.periodFrequencyList = data
    },
    setEmocActivity: (state, data) => {
      state.emocActivity = data
      state.emocActivityLength = data.length
      let setActivityTemp = {}
      data.map(dm => {
        setActivityTemp[dm.Name] = []
      })
      state.activitySet = setActivityTemp
    },
    setMonitoringResult: (state, data) => {
      state.monitoringResult = data
      state.monitoringResultLength = data.length
      let setMonitoringResultTemp = {}
      data.map(dm => {
        setMonitoringResultTemp[dm.Name] = []
      })
      state.monitoringResultSet = setMonitoringResultTemp
    },
    setEmocTemporaryHistory: (state, data) => {
      let setDefaultField = data
      state.emocTemporaryHistory = Object.assign(state.emocTemporaryHistory, setDefaultField)
    },
    setEmocTemporaryHistory2: (state, data) => {
      let setDefaultField = data
      state.emocTemporaryHistory2 = Object.assign(state.emocTemporaryHistory2, setDefaultField)
    },
    setBtnComfirmDisable: (state, data) => {
      state.btnComfirmDisable = data
    },
    setBtnComfirmDisable2: (state, data) => {
      state.btnComfirmDisable2 = data
    },
    setBtnComfirmDisable3: (state, data) => {
      state.btnComfirmDisable3 = data
    },
    setCorrectProblemDisable: (state, data) => {
      state.correctProblemDisable = data
    }
  },
  actions: {
    receiveLookupUser: ({commit}, {type, sendParam}) => {
      createEmocApi.getLookupUser(result => {
        commit('setLookupUser', {type, data: result})
      }, sendParam)
    },
    receiveLookupEmocNumber: ({commit}, sendParam) => {
      createEmocApi.getLookupEmocNumber(result => {
        commit('setLookupEmocNumber', result)
      }, sendParam)
    },
    receiveLookupNotification: ({commit}, sendParam) => {
      createEmocApi.getLookupNotification(result => {
        commit('setLookupNotification', result)
      }, sendParam)
    },
    receiveEmocApplication: ({commit}, sendParam) => {
      createEmocApi.getEmocApplication(result => {
        console.log('receiveEmocApplication', result)
        commit('setEmocApplication', result)
      }, sendParam)
    },
    receivePeriodFrequencyList: ({commit}) => {
      createEmocApi.getPeriodFrequencyList(result => {
        commit('setPeriodFrequencyList', result)
      })
    },
    receiveEmocActivity: ({dispatch, commit}, sendParam) => {
      createEmocApi.getEmocActivity(result => {
        console.log('receiveEmocActivity', result)
        commit('setEmocActivity', result)
      }, sendParam)
    },
    receiveMonitoringResult: ({commit}, sendParam) => {
      createEmocApi.getMonitoringResult(result => {
        console.log('receiveMonitoringResult', result)
        commit('setMonitoringResult', result)
      }, sendParam)
    },
    receiveEmocTemporaryHistory: ({commit}, sendParam) => {
      createEmocApi.getEmocTemporaryHistory(result => {
        console.log('receiveEmocTemporaryHistory', result)
        commit('setEmocTemporaryHistory', result)
      }, sendParam)
    },
    receiveEmocTemporaryHistory2: ({commit}, sendParam) => {
      createEmocApi.getEmocTemporaryHistory2(result => {
        console.log('receiveEmocTemporaryHistory2', result)
        commit('setEmocTemporaryHistory2', result)
      }, sendParam)
    },
    saveMonitoringResult: ({commit}, paramSave) => {
      return new Promise((resolve, reject) => {
        createEmocApi.saveMonitoringResult(result => {
          resolve(result)
        }, paramSave)
      })
    },
    saveCreateEmoc: ({commit}, paramSave) => {
      console.log('paramSave', paramSave)
      return new Promise((resolve, reject) => {
        createEmocApi.saveCreateEmoc(result => {
          resolve(result)
          commit('setEditEnable', false)
        }, paramSave)
      })
    },
    sendMsg: ({ commit }, paramMsg) => {
      createEmocApi.sendMsg(result => {
        if (result) {
          console.log('sendMsg successfully', result)
        }
      }, paramMsg)
    }
  }
})



