<template>
  <div name="activity-all">
    <!-- :closedOnStart="index === 0 ? false : true" -->
    <expandable-section-std v-for="(activitySet, index) in emocActivityOp" :title="'Activity ' + (index + 1)" :closedOnStart="!disabledStatusMonitoring" :ref="'expandActivity' + index" :key="'expandActivity' + index" >

      <div class="data-frame">

        <div class="row">
          <div class="col-sm-12">
            <div class="row">
              <div class="col-6">
                <div class="row">
                  <div class="col-12" >
                    <input-text-std
                      v-model="activitySet.Name"
                      :name="'Name' + index"
                      label="Activity"
                      :required="true"
                      disabled
                      :maxlength="80"
                    >
                    </input-text-std>
                  </div>
                </div>
              </div>

              <div class="col-3">
                <div class="row">
                  <div class="col-12">
                    <combobox-std
                      v-model="activitySet.Period_frequency__c"
                      :name="'Period_frequency__c' + index"
                      :options="periodFrequencyList"
                      label="Period/Frequency"
                      :required="true"
                      disabled
                    >
                    </combobox-std>
                  </div>
                </div>
              </div>
              <div class="col-3">
                <div class="row">
                  <div class="col-12">
                    <lookups-std v-model="activitySet.Action_by__c"
                           label="Action By"
                           placeholder="Search..."
                           :options="optionsLookupUser(index, activitySet)"
                           :name="'Action_by__c' + index"
                           @search="filterLookupUser('lookupUser' + index, arguments[0])"
                           @setDataRow="setDataRow(index, arguments[0])"
                           icon="people"
                           disabled
                           :required="true"
                           loading>
                    </lookups-std>
                  </div>
                </div>
              </div>
              <div class="col-6">
                <div class="row">
                  <div class="col-12">
                    <checkbox-std
                      v-model="activitySet.Acknowledge__c"
                      label="Acknowledge"
                      :inline="true"
                      :disabled="!editEnable"
                      @onChange="setAcknowledge(index, activitySet)"
                    />
                  </div>
                </div>
              </div>
              <div class="col-6">
                <div class="row">
                  <div class="col-12">
                    <lookups-std v-model="activitySet.Acknowledge_by__c"
                                 label="Acknowledge by"
                                 placeholder="Search..."
                                 :options="optionsLookupUser2(index, activitySet)"
                                 :name="'Acknowledge_by__c' + index"
                                 @search="filterLookupUser('lookupUser2' + index, arguments[0])"
                                 @setDataRow="setDataRow2(index, arguments[0])"
                                 icon="people" 
                                 disabled
                                 loading>
                    </lookups-std>
                  </div>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-12" style="margin-bottom: 10px;">
                <button v-if="editEnable" class="slds-button slds-button_neutral"  @click="callMonitoringResult(activitySet)">Edit Monitoring Result</button>
                <button v-else class="slds-button slds-button_neutral" disabled >Edit Monitoring Result</button>

                <button class="slds-button slds-button_neutral" @click="callViewMonitoringResult(activitySet)">View Monitoring Result</button>
              </div>
            </div>            
          </div>
        </div>
      </div>
    </expandable-section-std>

    <modal-std size="large" ref="refModalMonitoringResult" @close="afterClosePopup" id="monitoringResult">
      <template #header>
        <div style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">{{displayMonitoring.view ? 'View Monitoring Result' : 'Edit Monitoring Result'}}</span></div>
      </template>
      <template #content>
        <monitoring-result-popup v-if="displayMonitoring.create || displayMonitoring.view"
                                :displayMonitoring="displayMonitoring"
                                :emocApplicationId="emocApplicationId"
                                :emocActivityId="emocActivityId"
                                :emocActivityName="emocActivityName"
                                :flgCallSave="flgCallSave"
                                ref="refMonitoringResult"/>
      </template>
      <template #footer>
        <div style="text-align: center;">
          <button class="slds-button slds-button_neutral" @click="callBackMonitoring">{{ displayMonitoring.view ? 'Back' : 'Cancel' }}</button>
          <button v-show="displayMonitoring.create" class="slds-button slds-button_brand" @click="callSave">Save</button>
        </div>
      </template>
    </modal-std>

    <modal-after-save id="afterSaveMonitoringResult"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveMonitoringResult"/>

    <modal-waiting    ref="popupLoader"/>

  </div>
</template>
<script>
import monitoringResultPopup from './MonitoringResultPopup.vue'

export default {
  name: 'activity-all',
  props: [
    'emoc',
    'disabledNextStage',
    'editEnable',
    'disabledStatusMonitoring'
  ],
  data () {
    return {
      lookupUserType: 'lookupUser',
      lookupUserType2: 'lookupUser2',
      resultSuccess: {},
      emocApplicationId: '',
      emocActivityId: '',
      emocActivityName: '',
      displayMonitoring: {
        create: false,
        view: false
      },
      flgCallSave: false
    }
  },
  computed: {
    periodFrequencyList () { return this.$store.state.periodFrequencyList },
    emocActivity () { return this.$store.state.emocActivity },
    lookupUser () { return this.$store.state.lookupUser },
    emocActivityOp () {
      return this.emocActivity.map((df, idx) => {
        df['rowId'] = idx
        return df
      })
    }
  },
  watch: {
  },
  methods: {
    filterLookupUser (type, searchParam) {
      this.$store.dispatch('receiveLookupUser', {type, sendParam: searchParam})
    },
    optionsLookupUser (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser('lookupUser' + rowIndex, dataRow.Action_by__r)
        return []
      }
    },
    optionsLookupUser2 (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType2 + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType2 + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser('lookupUser2' + rowIndex, dataRow.Acknowledge_by__r)
        return []
      }
    },
    setDataRow (rowIndex, dataRow) {
      if (typeof dataRow.Action_by__c === 'undefined') {
        this.filterLookupUser('lookupUser' + rowIndex, '')
      }
    },
    setDataRow2 (rowIndex, dataRow) {
      if (typeof dataRow.Acknowledge_by__c === 'undefined') {
        this.filterLookupUser('lookupUser2' + rowIndex, '')
      }
    },
    setAcknowledge (rowIndex, dataRow) {
      dataRow.Acknowledge_by__c = (dataRow.Acknowledge__c === true) ? this.emoc.User_Login : ''
      if (typeof dataRow.Acknowledge_by__c !== 'undefined' && dataRow.Acknowledge_by__c !== '') {
        this.filterLookupUser('lookupUser2' + rowIndex, this.emoc.User_Login)
      }
    },
    showPopupMonitoring () {
      this.$refs.refModalMonitoringResult.onOpen()
    },
    afterClosePopup () {
      this.displayMonitoring.create = false
      this.displayMonitoring.view = false
      this.flgCallSave = false
      this.emocApplicationId = ''
      this.emocActivityId = ''
    },
    callBackMonitoring () {
      this.$nextTick(() => {
        this.$refs.refModalMonitoringResult.onClose()
      })
    },
    callMonitoringResult (dataRow) {
      this.displayMonitoring.create = true
      this.emocApplicationId = this.emoc.Id
      this.emocActivityId = dataRow.Id
      this.emocActivityName = dataRow.Name
      this.showPopupMonitoring()
      this.$store.dispatch('receiveMonitoringResult', {emocApplicationId: this.emoc.Id, emocActivityId: dataRow.Id, emocActivityName: dataRow.Name})
    },
    callViewMonitoringResult (dataRow) {
      this.displayMonitoring.view = true
      this.emocApplicationId = this.emoc.Id
      this.emocActivityId = dataRow.Id
      this.showPopupMonitoring()
      this.$store.dispatch('receiveMonitoringResult', {emocApplicationId: this.emoc.Id, emocActivityId: dataRow.Id, emocActivityName: dataRow.Name})
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSaveMonitoringResult.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    callSave () {
      this.flgCallSave = true
      let dataSave = this.$refs.refMonitoringResult.genDataSave
      let chkReqAll = dataSave.filter(df => {
        return (!df.Monitoring_result_Pass__c && !df.Monitoring_result_Not_Pass__c && df.flgDelete === false)
      })
      this.$refs.refMonitoringResult.$validator.validateAll().then((result) => {
        if (result && chkReqAll.length === 0) {
          this.showPopupLoader()
          this.$store.dispatch('saveMonitoringResult', JSON.stringify(dataSave)).then(result => {
            if (result.statusSuccess) {
              this.closePopupLoader()
              this.callBackMonitoring()
              this.$nextTick(() => {
                this.showPopupAfterSave()
                this.resultSuccess = result
              })
            }
          })
        }
      })
    }
  },
  components: {
    monitoringResultPopup
  }
}
</script>


