<template>
  <div name="monitoring-result-popup">
    <expandable-section-std  title="Monitoring Result" :closedOnStart="false" ref="expandMonitoring" key="expandMonitoring">
      <div class="data-frame">
        <div class="scrollbar-table">
          <table id="monitoring-result">
            <tr>
              <th width="5%" v-if="displayMonitoring.create"></th>
              <th width="5%">No.</th>
              <th width="25%">Activity</th>
              <th width="15%">Date Monitoring</th>
              <th width="5%">Pass</th>
              <th width="5%">Not Pass</th>
              <th width="20%">Monitoring Result Description</th>
              <th width="20%">Remark</th>
            </tr>
            <tr v-for="(monitoring, idx) in monitoringParentChild">
              <td class="textcenter" v-if="displayMonitoring.create">
                <div v-if="!monitoring.flgAdd" class="col-1 delete-mapping-optional padding-t0 textcenter" >
                  <i class="fa fa-trash-o icon-delete-mapping-optional-disable" aria-hidden="true"></i>
                </div>
                <div v-else class="col-1 delete-mapping-optional padding-t0 textcenter" :class="{'delete-mapping-optional-disabled': monitoring.flgDelete}">
                  <i class="fa fa-trash-o icon-delete-mapping-optional" @click="clickDelete(idx)" aria-hidden="true"></i>
                </div>
              </td>
              <td class="textcenter">{{idx + 1}}</td>
              <td>
                <div class="monitoring-list">
                  <input-text-std
                      v-model="monitoring.Name"
                      v-validate="(!monitoring.flgDelete) ? 'required' : ''" 
                      data-vv-as="Activity"
                      data-vv-value-path="$data.$_value"
                      :name="'Name' + idx"
                      label=""
                      :error="errors.has('Name' + idx)"
                      :required="true"
                      :disabled="displayMonitoring.view || monitoring.flgDelete || !monitoring.flgAdd"
                      :maxlength="80"
                    >
                      <template #error>
                        <span>{{ errors.first('Name' + idx) }}</span>
                      </template>
                    </input-text-std>

                </div>
              </td>
              <td class="textcenter"> 
                <div class="date-list">
                  <v-menu v-model="monitoring.showPickerMonitoringDate"
                          :close-on-content-click="false"
                          transition="scale-transition"
                          offset-y
                          max-width="290px"
                          min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="monitoring.Date_Monitoring__c"
                        v-validate="(!monitoring.flgDelete && monitoring.Date_Monitoring__c === '') ? 'required|date_format:dd/MM/yyyy' : 'date_format:dd/MM/yyyy'" 
                        data-vv-as="Date Monitoring"
                        data-vv-value-path="inputValue"
                        :name="'Date_Monitoring__c' + idx"
                        :error="errors.has('Date_Monitoring__c' + idx)"
                        @blur="setDateMonitoring(monitoring)"
                        placeholder="dd/mm/yyyy"
                        v-bind="attrs"
                        v-on="on"
                        append-icon="mdi-calendar"
                        autocomplete="off"
                        hide-details
                        outlined
                        dense
                        :disabled="displayMonitoring.view || monitoring.flgDelete || !monitoring.flgAdd"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      no-title
                      v-model="monitoring.dateMonitoring"
                      @change="setDateMonitoring2(monitoring)"
                      :min="minCalendar"
                    ></v-date-picker>
                  </v-menu>
                  <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Date_Monitoring__c' + idx) }}</span></div>
                </div>
              </td>
              <td class="textcenter">
                <checkbox-std v-model="monitoring.Monitoring_result_Pass__c" :disabled="displayMonitoring.view || monitoring.flgDelete || !monitoring.flgAdd"
                              :name="'Monitoring_result_Pass__c' + idx"
                              @onChange="clearAnotherCheckbox(idx, 'Monitoring_result_Pass__c', monitoring)"
                              :error="checkError(monitoring)" />
              </td>
              <td class="textcenter">
                <checkbox-std v-model="monitoring.Monitoring_result_Not_Pass__c" :disabled="displayMonitoring.view || monitoring.flgDelete || !monitoring.flgAdd"
                              :name="'Monitoring_result_Not_Pass__c' + idx"
                              @onChange="clearAnotherCheckbox(idx, 'Monitoring_result_Not_Pass__c', monitoring)"
                              :error="checkError(monitoring)" />
              </td>
              <td>
                <input-text-std style="min-width: 250px;"
                  v-model="monitoring.Monitoring_result_description__c"
                  v-validate="(!monitoring.flgDelete) ? 'required' : ''" 
                  data-vv-as="Monitoring Result Description"
                  data-vv-value-path="$data.$_value"
                  :name="'Monitoring_result_description__c' + idx"
                  label=""
                  :error="errors.has('Monitoring_result_description__c' + idx)"
                  :required="true"
                  :disabled="displayMonitoring.view || monitoring.flgDelete || !monitoring.flgAdd"
                >
                  <template #error>
                    <span>{{ errors.first('Monitoring_result_description__c' + idx) }}</span>
                  </template>
                </input-text-std>
              </td>
              <td>
                <input-text-std style="min-width: 250px;" v-model="monitoring.Remark__c" :disabled="displayMonitoring.view || monitoring.flgDelete || !monitoring.flgAdd"/>
              </td>
            </tr>
          </table>
    
        </div>
       
        <div class="row" v-if="displayMonitoring.create" style="padding-top: 15px;">
          <div class="col-sm-6">
            <button class="slds-button slds-button_neutral" @click="clickAdd"><i class="fa fa-plus" aria-hidden="true"></i>&nbsp;&nbsp;Add</button>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
import moment from 'moment'

export default {
  name: 'monitoring-result-popup',
  props: [
    'emocApplicationId', 'emocActivityId', 'displayMonitoring', 'emocActivityName', 'flgCallSave'
  ],
  data () {
    return {
      resultSuccess: {},
      monitoringParentChild: [],
      minCalendar: moment().format('YYYY-MM-DD')
    }
  },
  mounted () {
    this.remapFieldMonitoring()
  },
  computed: {
    monitoringResult () { return this.$store.state.monitoringResult },
    genDataSave () {
      let remap = []
      this.monitoringParentChild.map(dm => {
        remap.push({
          Id: dm.Id,
          E_MOC_Application_No__c: dm.E_MOC_Application_No__c !== '' ? dm.E_MOC_Application_No__c : this.emocApplicationId,
          E_MOC_Activity__c: dm.E_MOC_Activity__c !== '' ? dm.E_MOC_Activity__c : this.emocActivityId,
          Name: dm.Name,
          Date_Monitoring__c: (dm.Date_Monitoring__c !== '') ? this.parseDate(dm.Date_Monitoring__c) : null,
          Monitoring_result_Pass__c: dm.Monitoring_result_Pass__c || false,
          Monitoring_result_Not_Pass__c: dm.Monitoring_result_Not_Pass__c || false,
          Monitoring_result_description__c: dm.Monitoring_result_description__c || '',
          Remark__c: dm.Remark__c || '',
          flgAdd: dm.flgAdd || false,
          flgDelete: dm.flgDelete || false
        })
      })
      return remap
    }
  },
  watch: {
    monitoringResult () {
      this.remapFieldMonitoring()
    }
  },
  methods: {
    setDateMonitoring (dataRow) {
      dataRow.dateMonitoring = this.parseDate(dataRow.Date_Monitoring__c)
    },
    setDateMonitoring2 (dataRow) {
      dataRow.showPickerMonitoringDate = false
      dataRow.Date_Monitoring__c = this.formatDate(dataRow.dateMonitoring)
    },
    remapFieldMonitoring () {
      let remap = []
      if (this.displayMonitoring.create) {
        this.monitoringResult.map(dm => {
          remap.push({
            rowId: dm.rowId,
            Id: dm.Id,
            E_MOC_Application_No__c: dm.E_MOC_Application_No__c,
            E_MOC_Activity__c: dm.E_MOC_Activity__c,
            Name: dm.Name,
            Date_Monitoring__c: dm.Date_Monitoring__c,
            dateMonitoring: dm.dateMonitoring,
            showPickerMonitoringDate: false,
            Monitoring_result_Pass__c: dm.Monitoring_result_Pass__c,
            Monitoring_result_Not_Pass__c: dm.Monitoring_result_Not_Pass__c,
            Monitoring_result_description__c: dm.Monitoring_result_description__c,
            Remark__c: dm.Remark__c,
            flgAdd: dm.flgAdd,
            flgDelete: dm.flgDelete
          })
        })
      } else {
        // let noItem = 0
        this.monitoringResult.map(dm => {
          if (dm.Monitoring_result_Pass__c || dm.Monitoring_result_Not_Pass__c) {
            remap.push({
              rowId: dm.rowId,
              Id: dm.Id,
              E_MOC_Application_No__c: dm.E_MOC_Application_No__c,
              E_MOC_Activity__c: dm.E_MOC_Activity__c,
              Name: dm.Name,
              Date_Monitoring__c: dm.Date_Monitoring__c,
              dateMonitoring: dm.dateMonitoring,
              showPickerMonitoringDate: false,
              Monitoring_result_Pass__c: dm.Monitoring_result_Pass__c,
              Monitoring_result_Not_Pass__c: dm.Monitoring_result_Not_Pass__c,
              Monitoring_result_description__c: dm.Monitoring_result_description__c,
              Remark__c: dm.Remark__c,
              flgAdd: dm.flgAdd,
              flgDelete: dm.flgDelete
            })
          }
        })
      }
      this.monitoringParentChild = remap
    },
    clearAnotherCheckbox (idx, field, dataRow) {
      if (field === 'Monitoring_result_Pass__c' && dataRow[field]) {
        dataRow.Monitoring_result_Not_Pass__c = false
      }
      if (field === 'Monitoring_result_Not_Pass__c' && dataRow[field]) {
        dataRow.Monitoring_result_Pass__c = false
      }
    },
    checkError (dataRow) {
      if (this.flgCallSave) {
        if (dataRow.Monitoring_result_Pass__c || dataRow.Monitoring_result_Not_Pass__c || dataRow.flgDelete === true) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },
    clickAdd () {
      let lastIndex = this.monitoringParentChild.length
      this.monitoringParentChild.push({
        rowId: lastIndex,
        Id: null,
        E_MOC_Application_No__c: this.emocApplicationId,
        E_MOC_Activity__c: this.emocActivityId,
        Name: this.emocActivityName,
        Date_Monitoring__c: moment().format('DD/MM/YYYY'),
        Monitoring_result_Pass__c: false,
        Monitoring_result_Not_Pass__c: false,
        Monitoring_result_description__c: '',
        Remark__c: '',
        flgAdd: true,
        flgDelete: false
      })
    },
    clickDelete (rowId) {
      let setMonitoringResult = JSON.parse(JSON.stringify(this.monitoringParentChild))
      setMonitoringResult[rowId]['flgDelete'] = !setMonitoringResult[rowId]['flgDelete']
      this.$nextTick(() => {
        this.$store.commit('setMonitoringResult', setMonitoringResult)
      })
    },
    parseDate (date, monitoring) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate (date) {
      if (!date) return null
      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    }
  }
}
</script>

<style scope>
  #monitoring-result td, #monitoring-result th {
    border: 1px solid #ddd;
    padding: 5px;
  }
  #monitoring-result th {
    background-color: #fafaf9;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
  }
  #monitoring-result td>.monitoring-list {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 300px;
    max-width: 700px;
  }
  #monitoring-result td>.date-list {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 160px;
    max-width: 160px;
  }
  .textcenter {
    text-align: center;
  }
  #monitoring-result tr:hover {background-color: #f2f2f2;}
  #monitoring-result .slds-form-element__label {display: none;}
  #monitoring-result .slds-checkbox_faux {margin-right: 0px;}
  .scrollbar-table {
    overflow: auto;
  }
  .column-revision { min-width: 91px; text-align: center; }
  .column-last-modified { min-width: 200px; text-align: center; }

  .delete-mapping-optional {
    font-size: large;
    color: #0176d3;
  }
  .delete-mapping-optional-disabled {
    color: rgb(234, 0, 30);
  }
  .delete-mapping-optional
    .icon-delete-mapping-optional {
      cursor: pointer;
    }
    .icon-delete-mapping-optional-disable {
      color: #c9c9c9;
      cursor: not-allowed;
    }
/*  .slds-input-has-icon_right .slds-input {
    text-decoration: line-through;
  }*/
  .padding-b0{
    padding-bottom: 0px;
  }
  .padding-t0{
    padding-top: 0px;
  }
</style>


