<template>
  <div name="modal-waiting" class="modal-waiting">
    <modal-std :id="id"
               size="small"
               :btnCloseDisp="false"
               @close="$emit('close')"
               ref="modalLoader">
      <template #content>
        <div class="custom-modal-loader">
          <button title="loading" id="loadingPageId">
            <img :src="require('../assets/loader.gif')" style="width: 80px"/>
            <p style="margin-top: 20px;"><span class="slds-card__header-link slds-text-heading_small">Please wait...</span></p>
          </button>
        </div>
      </template>
    </modal-std>
  </div>
</template>
<script>
export default {
  name: 'modal-waiting',
  props: {
    id: String
  },
  methods: {
    onOpen () {
      this.$refs.modalLoader.onOpen()
    },
    onClose () {
      this.$refs.modalLoader.onClose()
    }
  }
}
</script>

<style scoped>
  .custom-modal-loader {
    text-align: center;
    margin: 20;
  }
</style>



