<template>
  <div name="require-demolish">
    <expandable-section-std title="Require to demolish/Return to normal?" ref="expandRequireDemolish" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
     
          <div class="col-6">
            <div class="row">
              <div class="col-4">
                <checkbox-std
                  v-model="emoc.Yes_Require_to_demolish__c"
                  label="Yes"
                  :inline="true"
                  :disabled="!editEnable || !emoc.checkFirstConfirmDemolish"
                />
              </div>
            </div>
          </div>
          <div class="col-6">
            <div class="row">
              <div class="col-4">
                <checkbox-std
                  v-model="emoc.No_Require_to_demolish__c"
                  label="No"
                  name="No_Require_to_demolish__c"
                  :inline="true"
                  :disabled="!editEnable || !emoc.checkFirstConfirmDemolish"
                />
              </div>
            </div>
            <div class="row" v-if="emoc.No_Require_to_demolish__c">
              <div class="col-6" v-if="!editEnable">
                <label class="slds-form-element__label">EMOC Number for permanent MOC</label>
                <div v-if="emoc.EMOCNumberForPermanentMocLink !== ''">
                  <a :href="emoc.EMOCNumberForPermanentMocLink" target="_blank">{{emoc.EMOCNumberForPermanentMocName}}</a>
                </div>
              </div>
              <div class="col-6" v-else>
                <lookups-std v-model="emoc.EMOC_Number_for_permanent_MOC__c"
                             v-validate="setRuleNoDemolish" 
                             data-vv-as="EMOC Number for permanent MOC"
                             data-vv-value-path="inputValue"
                             label="EMOC Number for permanent MOC"
                             placeholder="Search..."
                             :options="optionsLookupEmocNumber"
                             :error="errors.has('EMOC_Number_for_permanent_MOC__c')"
                             name="EMOC_Number_for_permanent_MOC__c"
                             @search="filterLookupEmocNumber(arguments[0])"
                             icon="custom"
                             :disabled="!editEnable"
                             loading>
                  <template #error>
                    <span>{{ errors.first('EMOC_Number_for_permanent_MOC__c') }}</span>
                  </template>
                </lookups-std>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-show="checkRequireDemolish" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          <!-- {{'Require to demolish/Return to normal? is required'}} -->
        </div>
      </div>
    </expandable-section-std>

  </div>
</template>
<script>

export default {
  name: 'require-demolish',
  props: [
    'emoc',
    'recId',
    'checkRequireDemolish',
    'disabledNextStage',
    'editEnable'
  ],
  data () {
    return {
    }
  },
  created () {
    if (this.emoc.EMOC_Number_for_permanent_MOC__c !== '' && this.emoc.EMOC_Number_for_permanent_MOC__c) {
      this.filterLookupEmocNumber(this.emoc.EMOC_Number_for_permanent_MOC__c)
    }
    // if (this.emoc.Notification_for_Permanent__c !== '' && this.emoc.Notification_for_Permanent__c) {
    //   this.filterLookupNotification(this.emoc.Notification_for_Permanent__c)
    // }
  },
  computed: {
    emocTemporaryHistory2 () { return this.$store.state.emocTemporaryHistory2 },
    lookupEmocNumber () { return this.$store.state.lookupEmocNumber },
    optionsLookupEmocNumber () {
      return this.lookupEmocNumber.map(dm => {
        return {
          value: dm.Id,
          label: dm.Name,
          icon: 'custom'
        }
      })
    },
    // lookupNotification () { return this.$store.state.lookupNotification },
    // optionsLookupNotification () {
    //   return this.lookupNotification.map(dm => {
    //     return {
    //       value: dm.Id,
    //       label: dm.Name,
    //       icon: 'notification'
    //     }
    //   })
    // },
    setRuleNoDemolish () {
      // (this.emoc.No_Require_to_demolish__c === true && this.emoc.checkFirstConfirmDemolish === false) ||
      return (this.emoc.Not_Extend_Require_to_extend_to_prevent__c === true) ? 'required' : ''
    }
  },
  watch: {
    'emoc.No_Require_to_demolish__c' (newValue) {
      if (newValue) {
        this.emoc.Yes_Require_to_demolish__c = false
      }
    },
    'emoc.Yes_Require_to_demolish__c' (newValue) {
      if (newValue) {
        this.emoc.No_Require_to_demolish__c = false
      }
    },
    'emoc.EMOC_Number_for_permanent_MOC__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupEmocNumber(newVal)
      }
    }
    // 'emoc.Notification_for_Permanent__c' (newVal) {
    //   if (newVal !== '' && newVal) {
    //     this.filterLookupNotification(newVal)
    //   }
    // }
  },
  methods: {
    filterLookupEmocNumber (searchParam) {
      this.$store.dispatch('receiveLookupEmocNumber', searchParam)
    }
    // filterLookupNotification (searchParam) {
    //   this.$store.dispatch('receiveLookupNotification', searchParam)
    // }
  }
}
</script>


