<template>
  <div name="temporary-workflow">

    <monitoring-program :emoc="emoc" 
                        :recId="recId"
                        :checkRequiredMonitoringProgram="checkRequiredMonitoringProgram"
                        :disabledStatusMonitoring="disabledStatusMonitoring"
                        :editEnable="editEnable"
                        ref="monitoringProgramCmp" />

    <activity           v-if="showSectionActivity" 
                        :emoc="emoc"
                        :disabledSectionActivity="disabledSectionActivity"
                        :editEnable="editEnable"
                        :disabledStatusMonitoring="disabledStatusMonitoring"
                        ref="activityCmp" /> 

    <activity-all       v-if="showSectionActivityAll"
                        :emoc="emoc"
                        :disabledNextStage="disabledNextStage"
                        :editEnable="editEnable"
                        :disabledStatusMonitoring="disabledStatusMonitoring"
                        ref="activityAllCmp" />

    <correct-problem     v-if="showSectionCorrectProblem"
                        :emoc="emoc" 
                        :recId="recId"
                        :checkRequiredCorrectProblem="checkRequiredCorrectProblem"
                        :disabledNextStage="disabledNextStage"
                        :editEnable="editEnable"
                        :showSectionCorrectProblem="showSectionCorrectProblem"
                        :clickSaveMain="clickSaveMain"
                        :checkStatusIsFold="checkStatusIsFold"
                        ref="correctProblemCmp" />
                        
    <extend-temporary   v-if="showSectionExtendTemporary" 
                        :emoc="emoc" 
                        :recId="recId"
                        :checkRequiredExtendTemporary="checkRequiredExtendTemporary"
                        :disabledNextStage="disabledNextStage"
                        :editEnable="editEnable"
                        :clickSaveMain="clickSaveMain"
                        ref="extendTemporaryCmp" />

    <return-temporary   v-if="showSectionReturnTemporaryMoc" 
                        :emoc="emoc" 
                        :editEnable="editEnable"
                        ref="returnTemporaryMOCCmp" />

    <require-demolish   v-if="showSectionRequireDemolish" 
                        :emoc="emoc" 
                        :recId="recId"
                        :checkRequireDemolish="checkRequireDemolish"
                        :disabledNextStage="disabledNextStage"
                        :editEnable="editEnable"
                        ref="extendRequireDemolishCmp" />

    <extend-prevent     v-if="showSectionExtendPrevent"
                        :emoc="emoc" 
                        :recId="recId"
                        :checkRequireExtendPreventoverdue="checkRequireExtendPreventoverdue"
                        :disabledNextStage="disabledNextStage"
                        :editEnable="editEnable"
                        :clickSaveMain="clickSaveMain"
                        ref="extendRequireExtendPreventoverdueCmp" />

  </div>
</template>
<script>

import monitoringProgram from './MonitoringProgramRequirement.vue'
import activity from './Activity.vue'
import activityAll from './ActivityAll.vue'
import correctProblem from './CorrectProblem.vue'
import extendTemporary from './RequireExtendTemporary.vue'
import requireDemolish from './RequireDemolish.vue'
import extendPrevent from './RequireExtendPrevent.vue'
import returnTemporary from './ReturnTemporaryMOCBackToNormal.vue'

export default {
  name: 'temporary-workflow',
  props: ['emoc', 'recId', 'showSectionCorrectProblem', 'emocEditEnable', 'checkRequiredMonitoringProgram', 'editEnable', 'checkRequiredCorrectProblem', 'checkRequiredExtendTemporary', 'checkRequireDemolish', 'checkRequireExtendPreventoverdue', 'showSectionActivity', 'showSectionActivityAll', 'showSectionExtendTemporary', 'showSectionRequireDemolish', 'showSectionExtendPrevent', 'clickSaveMain', 'showSectionReturnTemporaryMoc'],
  data () {
    return {
      items: []
    }
  },
  computed: {
    disabledStatusMonitoring () {
      return this.emoc.Status_Common_Workflow__c === 'Monitoring'
    },
    disabledNextStage () {
      return false
      // return ['Detailed Design/Study', 'Hazard Screening', 'Technical Review'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledSectionActivity () {
      return (this.emoc.Status_Common_Workflow__c === 'VP Approval' || this.emoc.Status_Common_Workflow__c === 'Implementation' || this.emoc.Status_Common_Workflow__c === 'Readiness Review')
    },
    checkStatusIsFold () {
      return ['Hazard Screening', 'Technical Review', 'APU Approval', 'VP Approval'].indexOf(this.emoc.Status_Common_Workflow__c) !== -1
    }
  },
  methods: {
  },
  components: {
    monitoringProgram,
    activity,
    activityAll,
    correctProblem,
    extendTemporary,
    requireDemolish,
    extendPrevent,
    returnTemporary
  }
}
</script>


