<template>
  <div name="extend-temporary">
    <expandable-section-std title="Require to extend this temporary MOC?" ref="expandExtendTemporary" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          
          <div class="col-6">
            <div class="row">
              <div class="col-12">
                <checkbox-std
                  v-model="emoc.Extend_Require_to_extend__c"
                  name="Extend_Require_to_extend__c"
                  label="Extend"
                  :inline="true"
                  :disabled="!editEnable"
                />
              </div>
            </div>
             <!-- || !emoc.Show_Correct_the_problem_section__c -->
          </div>
          <div class="col-6">
            <div class="row">
              <div class="col-12">
                <checkbox-std
                  v-model="emoc.Not_Extend_Require_to_extend__c"
                  label="Not Extend"
                  name="Not_Extend_Require_to_extend__c"
                  :inline="true"
                  :disabled="!editEnable"
                />
              </div>
            </div>
             <!-- || !emoc.Show_Correct_the_problem_section__c -->
            <div class="row" v-if="emoc.Not_Extend_Require_to_extend__c">
              <div class="col-12">
                <textarea-std
                  v-model="emoc.Reason_for_Extension_temporary_MOC__c"
                  name="Reason_for_Extension_temporary_MOC__c"
                  label="Remark"
                  :rows="1"
                  disabled
                >
                </textarea-std>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div v-show="checkRequiredExtendTemporary" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          {{'The required to extend this temporary MOC field is required.'}}
        </div>
      </div>
    </expandable-section-std>

    <modal-std size="large" ref="refModalExtendTemporary" @close="afterClosePopup" id="extendTemporary">
      <template #header>
        <div style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">Require to {{labelTypeCheckbox}} this temporary MOC</span></div>
      </template>
      <template #content>
        <extend-temporary-popup :emoc="emoc"
                                :labelTypeCheckbox="labelTypeCheckbox"
                                ref="refExtendTemporary"/>
      </template>
      <template #footer>
        <div style="text-align: right;">
          <button class="slds-button slds-button_neutral" @click="callBackExtendTemporary">Cancel</button>
          <button v-if="btnComfirmDisable3" class="slds-button slds-button_brand" @click="callSave">Comfirm</button>
          <button v-else class="slds-button slds-button_brand" disabled @click="callSave">Comfirm</button>
        </div>
      </template>
    </modal-std>

    <modal-after-save id="afterSaveExtendTemporary"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveExtendTemporary"/>
  </div>
</template>
<script>
import extendTemporaryPopup from './RequireExtendTemporaryPopup.vue'

export default {
  name: 'extend-temporary',
  props: [
    'emoc',
    'recId',
    'checkRequiredExtendTemporary',
    'disabledNextStage',
    'editEnable',
    'clickSaveMain'
  ],
  data () {
    return {
      labelTypeCheckbox: ''
    }
  },
  computed: {
    btnComfirmDisable3 () { return this.$store.state.btnComfirmDisable3 }
    // emoc () { return this.$store.state.emoc }
  },
  watch: {
    'emoc.Not_Extend_Require_to_extend__c' (newValue) {
      if (newValue) {
        this.emoc.Extend_Require_to_extend__c = false
        if (newValue === true) {
          this.showPopupExtendTemporary()
          this.labelTypeCheckbox = 'Not Extend'
          this.$store.commit('setBtnComfirmDisable3', true)
        }
      }
    },
    'emoc.Extend_Require_to_extend__c' (newValue) {
      if (newValue) {
        this.emoc.Not_Extend_Require_to_extend__c = false
        if (newValue === true) {
          this.showPopupExtendTemporary()
          this.labelTypeCheckbox = 'Extend'
          this.$store.commit('setBtnComfirmDisable3', this.emoc.newEndDateTempoHis !== null && this.emoc.newEndDateTempoHis !== '' && this.emoc.Comfirm === true)
        }
      }
    }
  },
  methods: {
    showPopupExtendTemporary () {
      this.$refs.refModalExtendTemporary.onOpen()
    },
    afterClosePopup () {
    },
    callBackExtendTemporary () {
      this.$nextTick(() => {
        this.$refs.refModalExtendTemporary.onClose()
      })
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSaveExtendTemporary.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    callSave () {
      this.callBackExtendTemporary()
      if (this.emoc.Extend_Require_to_extend__c) {
        this.clickSaveMain()
      }
      // this.$store.dispatch('saveExtendTemporary', JSON.stringify(this.$refs.refExtendTemporary.genDataSave)).then(result => {
      //   if (result.statusSuccess) {
      //     this.callBackExtendTemporary()
      //     this.$nextTick(() => {
      //       this.showPopupAfterSave()
      //       this.resultSuccess = result
      //     })
      //   }
      // })
    }
  },
  components: {
    extendTemporaryPopup
  }
}
</script>


