<template>
  <div name="return-temporary-moc-back-to-normal">
    <expandable-section-std title="Return Temporary MOC back to normal" ref="showSectionExtendTemporary" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <checkbox-std
              v-model="emoc.Return_to_Normal__c"
              label="Return to Normal"
              :inline="true"
              :disabled="!editEnable"
              @onChange="setActionBy(emoc.Return_to_Normal__c)"
            />
          </div>
          <div class="col-md-6">
            <lookups-std v-model="emoc.Action_by_return_to_normal__c"
                         label="Action by"
                         placeholder="Search..."
                         :options="optionsLookupUser()"
                         name="Action_by_return_to_normal__c"
                         @search="filterLookupUser(arguments[0])"
                         icon="people"
                         disabled
                         loading>
            </lookups-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>

export default {
  name: 'return-temporary-moc-back-to-normal',
  props: [
    'emoc', 'editEnable'
  ],
  data () {
    return {
      nameRules: [
        v => !!v || 'Name is required'
      ],
      lookupType: 'returnTempo'
    }
  },
  created () {
    if (this.emoc.Action_by_return_to_normal__c !== '' && this.emoc.Action_by_return_to_normal__c) {
      this.filterLookupUser(this.emoc.Action_by_return_to_normal__c)
    }
  },
  computed: {
    lookupUser () { return this.$store.state.lookupUser }
  },
  watch: {
    'emoc.Action_by_return_to_normal__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    }
  },
  methods: {
    setActionBy (val) {
      this.emoc.Action_by_return_to_normal__c = (this.emoc.Return_to_Normal__c === true) ? this.emoc.User_Login : ''
      if (typeof this.emoc.Action_by_return_to_normal__c !== 'undefined' && this.emoc.Action_by_return_to_normal__c !== '') {
        this.filterLookupUser(this.emoc.User_Login)
      }
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    optionsLookupUser () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people'
          }
        })
      } else {
        return []
      }
    }
  }
}
</script>


