<template>
  <div class="slds-section" name="expandable-section-standard">
    <div :class="{ 'expandable-is-open': (isOpen || nonCollapsible) && !disabled }">
      <div v-if="nonCollapsible" class="section-title">
        <i class="fa fa-angle-down icon-angle-down"></i>
        <span :title="title">{{ title }}</span>
        <div v-show="helpText" class="slds-form-element__icon help-text-icon" style="left:10px;">
          <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
            <i class="fa fa-info-circle" aria-hidden="true"></i>
            <span class="slds-assistive-text">Help</span>
          </button>
          <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-50px;left:-15px;width: 300px;">
            <div class="slds-popover__body">{{helpText}}</div>
          </div>
        </div>
      </div>
      <button v-else class="section-title slds-button" :disabled="disabled" @click="toggle">
        <i class="fa fa-angle-down icon-angle-down" :class="{ 'icon-angle-down-is-open': (isOpen || nonCollapsible) && !disabled }"></i>
        <span :title="title">{{ title }}</span>
        <div v-show="helpText" class="slds-form-element__icon help-text-icon" style="left:10px;">
          <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
            <i class="fa fa-info-circle" aria-hidden="true"></i>
            <span class="slds-assistive-text">Help</span>
          </button>
          <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-50px;left:-15px;width: 300px;">
            <div class="slds-popover__body">{{helpText}}</div>
          </div>
        </div>
      </button>
      <transition @before-enter="collapse" @enter="expand" @after-enter="resetHeight" @before-leave="expand" @leave="collapse">
        <div v-show="isOpen && !disabled" name="expandable-section-content" :id="'content-' + unique_id" class="section-content">
          <slot />
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'expandable-section-standard',
  props: {
    closedOnStart: Boolean,
    disabled: Boolean,
    nonCollapsible: Boolean,
    title: { type: String, required: true },
    helpText: String
  },
  data () {
    return {
      unique_id: this._uid,
      isOpen: false,
      initialized: false
    }
  },
  async mounted () {
    this.isOpen = !this.closedOnStart
    this.initialized = true
    // if (this.isOpen) {
    //   const elExpSecConArray = document.getElementsByName('expandable-section-content')
    //   console.log('elExpSecConArray', elExpSecConArray)
    //   elExpSecConArray.map((dm, idx) => {
    //     this.expand(elExpSecConArray[idx])
    //   })
    // }
  },
  methods: {
    collapse (element) {
      if (!this.initialized) return
      this.$nextTick(() => {
        element.style.height = 0
        element.style.opacity = 0
        element.style.overflow = 'hidden'
      })
    },
    expand (element) {
      if (!this.initialized) return
      this.$nextTick(() => {
        element.style.height = element.scrollHeight + 'px'
        element.style.opacity = 1
        element.style.overflow = 'hidden'
      })
    },
    resetHeight (element) {
      this.$nextTick(() => {
        element.style.height = null
        element.style.overflow = 'initial'
      })
    },
    toggle () {
      if (this.isOpen) {
        let elementById = document.getElementById('content-' + this.unique_id)
        elementById.style.height = elementById.scrollHeight + 'px'
      }
      this.$nextTick(() => {
        this.isOpen = !this.isOpen
      })
    }
  }
}
</script>

<style scoped>
  .slds-section {
    margin-right: 2px;
    margin-left: 2px;
  }
  .section-title {
    display: flex;
    align-items: center;
    background: #f3f3f3;
    cursor: pointer;
    width: 100%;
    text-align: left;
    padding: 0 0.5rem;
    font-size: 1rem;
    line-height: 1.875rem;
    border: 1px solid transparent;
    border-radius: 0.25rem;
    color: black;
  }
  .section-title:disabled {
    cursor: not-allowed;
  }
  .section-content {
    padding-top: 10px;
    padding-left: 15px;
    padding-right: 15px;
    transition:all 0.5s ease-in-out;
  }
  .icon-angle-down {
    transition: transform 0.5s;
    font-size: 20px;
    margin-right: 0.5rem;
    transform: rotate(-90deg);
  }
  /* .expandable-is-open */
  .icon-angle-down-is-open {
    transform: rotate(0deg);
  }
</style>



