<template>
  <div name="require-extend-prevent-popup">
    <div class="row margin-t15">
      <div class="col-md-4">
        <label class="slds-form-element__label">
          <abbr class="slds-required" title="required">*</abbr>Last end date
        </label>
        <v-menu v-model="showPickerEndDate"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="emoc.End_date__c"
              data-vv-as="Last end date"
              data-vv-value-path="inputValue"
              name="LastEnd_date__c"
              :error="errors.has('LastEnd_date__c')"
              @blur="dateEnd = parseDate(emoc.End_date__c)"
              placeholder="dd/mm/yyyy"
              v-bind="attrs"
              v-on="on"
              append-icon="mdi-calendar"
              autocomplete="off"
              hide-details
              outlined
              dense
              disabled
            ></v-text-field>
          </template>
          <v-date-picker
            no-title
            v-model="dateEnd"
            @change="showPickerEndDate = false"
            :min="minCalendar"
          ></v-date-picker>
        </v-menu>
      </div>

      <div class="col-4">
        <label class="slds-form-element__label">
          <abbr class="slds-required" title="required">*</abbr>New end date
        </label>
        <div  class="slds-form-element__icon help-text-icon">
          <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
            <i class="fa fa-info-circle" aria-hidden="true"></i>
            <span class="slds-assistive-text">Help</span>
          </button>
          <div class="slds-popover slds-popover_tooltip slds-nubbin_left help-text-display" role="tooltip" id="help" style="position:absolute;top:-13px;left:26px;">
            <div class="slds-popover__body">Maximum 6 months</div>
          </div>
        </div>

        <v-menu v-model="showPickerNewEndDate"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="emoc.newEndDateTempoHis"
              v-validate="setRuleDateEnd"
              data-vv-as="New end date"
              data-vv-value-path="inputValue"
              name="newEndDateTempoHis"
              :error="errors.has('newEndDateTempoHis')"
              @blur="newDateEnd = parseDate(emoc.newEndDateTempoHis)"
              placeholder="dd/mm/yyyy"
              v-bind="attrs"
              v-on="on"
              append-icon="mdi-calendar"
              autocomplete="off"
              hide-details
              outlined
              dense
            ></v-text-field>
          </template>
          <v-date-picker
            no-title
            v-model="newDateEnd"
            @change="showPickerNewEndDate = false"
            :min="minCalendar"
            :max="maxEndDate"
          ></v-date-picker>
        </v-menu>
        <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('newEndDateTempoHis') }}</span></div>
      </div>
      <div class="col-12">
        <checkbox-std
          v-model="emoc.Comfirm"
          label="Confirm to extend and change workflow to Hazard screening."
          :inline="true"
          @onChange="chekBtnDisable(emoc.Comfirm)"
        />
      </div>
    </div>
  </div>
</template>
<script>

import moment from 'moment'

export default {
  name: 'require-extend-prevent-popup',
  props: ['emocTemporaryHistory2', 'labelTypeCheckbox', 'emoc'],
  data () {
    return {
      resultSuccess: {},
      showPickerEndDate: false,
      showPickerNewEndDate: false,
      dateEnd: '',
      newDateEnd: '',
      minCalendar: moment().format('YYYY-MM-DD')
    }
  },
  mounted () {
  },
  computed: {
    genDataSave () {
      return this.emocTemporaryHistory2
    },
    setRuleDateEnd () {
      return 'required|date_format:dd/MM/yyyy|date_between:' + this.checkDateAfter + ',' + this.checkDateMax
    },
    checkDateAfter () {
      return moment(this.emoc.End_date__c, 'DD/MM/YYYY').add(-1, 'days').format('DD/MM/YYYY')
    },
    checkDateMax () {
      return moment(this.emoc.End_date__c, 'DD/MM/YYYY').add(181, 'days').format('DD/MM/YYYY')
    },
    maxEndDate () {
      let endDate = moment(this.emoc.End_date__c, 'DD/MM/YYYY').add(180, 'days').format('DD/MM/YYYY')
      return moment(endDate, 'DD/MM/YYYY').format('YYYY-MM-DD')
    }
  },
  watch: {
    dateEnd (val) {
      this.emoc.End_date__c = this.formatDate(this.dateEnd)
    },
    newDateEnd (val) {
      this.emoc.newEndDateTempoHis = this.formatDate(this.newDateEnd)
      this.chekBtnDisable(val)
    }
  },
  methods: {
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate (date) {
      if (!date) return null
      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    chekBtnDisable (val) {
      this.$nextTick(() => {
        if (this.emocTemporaryHistory2.Yes2__c) {
          this.$store.commit('setBtnComfirmDisable2', this.emoc.newEndDateTempoHis !== null && this.emoc.newEndDateTempoHis !== '' && this.emoc.Comfirm === true)
          // this.$store.commit('setBtnComfirmDisable2', this.emocTemporaryHistory2.Remark__c !== '' && this.emocTemporaryHistory2.Date__c !== null && this.emocTemporaryHistory2.Date__c !== '' && this.emocTemporaryHistory2.Comfirm === true)
        }
      })
    }
  }
}
</script>

<style scope>
</style>


