<template>
  <div name="correct-problem-popup">
    <div class="row">
      <div class="col-12">
        <textarea-std
          v-model="emocTemporaryHistory.Remark__c"
          v-validate="'required'"
          data-vv-as="Complete this "
          data-vv-value-path="inputValue"
          name="Remark__c"
          :label="emocTemporaryHistory.Yes__c ? 'Additional recommendation on top of existing solution' : 'Reason'"
          :error="errors.has('Remark__c')"
          :required="true"
          :rows="1"
          @onChange="chekBtnDisable(emocTemporaryHistory.Remark__c)"
        >
          <template #error>
            <span>{{ errors.first('Remark__c') }}</span>
          </template>
        </textarea-std>
      </div>
      <div class="col-12" v-if="emocTemporaryHistory.No__c">
        <checkbox-std
          v-model="emocTemporaryHistory.Comfirm"
          label="If select No, it will go to Detailed design/Study"
          :inline="true"
          @onChange="chekBtnDisable(emocTemporaryHistory.Comfirm)"
        />
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'correct-problem-popup',
  props: ['emocTemporaryHistory', 'labelTypeCheckbox'],
  data () {
    return {
      resultSuccess: {}
    }
  },
  mounted () {
  },
  computed: {
    genDataSave () {
      return this.emocTemporaryHistory
    }
  },
  watch: {
  },
  methods: {
    chekBtnDisable (val) {
      this.$nextTick(() => {
        if (this.emocTemporaryHistory.Yes__c) {
          this.$store.commit('setBtnComfirmDisable', this.emocTemporaryHistory.Remark__c !== '')
        } else if (this.emocTemporaryHistory.No__c) {
          this.$store.commit('setBtnComfirmDisable', this.emocTemporaryHistory.Remark__c !== '' && this.emocTemporaryHistory.Comfirm === true)
        }
      })
    }
  }
}
</script>

<style scope>
</style>


