<template>
  <div class="create-temporary">
    <div class="slds-card__header slds-grid header custom-border-header">
      <header class="slds-media slds-media_center slds-has-flexi-truncate">
        <div class="slds-media__body" >
          <div class="row" v-if="!editEnable">
            <div class="col-md-12 btn-footer">
              <button class="slds-button slds-button_neutral edit-btn" @click="clickEdit">Edit</button>
            </div>
          </div>
        </div>
      </header>
    </div>

    <modal-waiting      ref="popupLoader"/>

    <modal-after-save @close="closePopupSuccess"
                        ref="popupAfterSave"/>

    <temporary-workflow   :emocEditEnable="emocEditEnable"
                          :emoc="emoc"
                          :recId="recId"
                          :showSectionCorrectProblem="emocTemporaryHistory.showSectionCorrectProblem"
                          :checkRequiredMonitoringProgram="checkRequiredMonitoringProgram"
                          :checkRequiredCorrectProblem="checkRequiredCorrectProblem"
                          :checkRequiredExtendTemporary="checkRequiredExtendTemporary"
                          :checkRequireDemolish="checkRequireDemolish"
                          :checkRequireExtendPreventoverdue="checkRequireExtendPreventoverdue"
                          :showSectionActivity="showSectionActivity"
                          :showSectionActivityAll="showSectionActivityAll"
                          :showSectionExtendTemporary="showSectionExtendTemporary"
                          :showSectionRequireDemolish="showSectionRequireDemolish"
                          :showSectionExtendPrevent="showSectionExtendPrevent"
                          :showSectionReturnTemporaryMoc="showSectionReturnTemporaryMoc"
                          :editEnable="editEnable"
                          :clickSaveMain="clickSave"
                          ref="temporaryWorkflow"/>

    <footer class="slds-card__footer" v-if="editEnable">

      <div class="row">
        <div class="col-md-4"></div>
        <div class="col-md-4">
          <popover-error-std v-show="hasError"
                             :errorBagItems="errorBagItems"
                             :getLabelFields="getLabelFields"
                             :focusField="focusField"
                             ref="popoverErrorStd"/>
        </div>
        <div class="col-md-4 btn-footer">
          <button class="slds-button slds-button_brand" @click="clickSave">Save</button>
        </div>
      </div>
    </footer>
  </div>
</template>
<script>
import temporaryWorkflow from './TemporaryWorkflow.vue'
import moment from 'moment'

export default {
  name: 'create-temporary',
  data () {
    return {
      numpage: 3,
      hasError: false,
      errorBagItems: [],
      recId: '',
      resultSuccess: {},
      cpmCapexNextFalse: false,
      currentDate: moment().format('YYYY-MM-DD')
    }
  },
  created () {
    this.$store.dispatch('receiveLookupUser', { type: 'main', sendParam: '' })
    this.$store.dispatch('receiveLookupEmocNumber', '')
    this.$store.dispatch('receiveLookupNotification', '')
    this.$store.dispatch('receivePeriodFrequencyList')
  },
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    this.recId = urlParams.get('id')
    let paramSearch = {
      emocApplicationId: this.recId
    }
    if (this.emocEditEnable) {
      this.$store.dispatch('receiveEmocApplication', paramSearch)
      this.$store.dispatch('receiveEmocActivity', paramSearch)
      this.$store.dispatch('receiveEmocTemporaryHistory', paramSearch)
      this.$store.dispatch('receiveEmocTemporaryHistory2', paramSearch)
    }
  },
  computed: {
    emoc () { return this.$store.state.emoc },
    editEnable () { return this.$store.state.editEnable },
    emocTemporaryHistory () { return this.$store.state.emocTemporaryHistory },
    emocTemporaryHistory2 () { return this.$store.state.emocTemporaryHistory2 },
    emocEditEnable () {
      return this.recId && this.recId !== ''
    },
    checkRequiredMonitoringProgram () {
      return (this.emoc.Id !== '' && this.emoc.Id) && !(this.emoc.Yes_Monitoring_Program_Requirement__c || this.emoc.No_Monitoring_Program_Requirement__c) && this.editEnable
    },
    checkRequiredCorrectProblem () {
      return this.emocTemporaryHistory.showSectionCorrectProblem && !(this.emocTemporaryHistory.Yes__c || this.emocTemporaryHistory.No__c) && this.editEnable
    },
    showSectionExtendTemporary () { // น้อยกว่าเท่ากับ 30 // วันที่ปัจจุบัน มากกว่า Show_Require_to_Extend__c (เกิดจาก End_Date_Monitoring_Program__c ลบไป 30 วัน)
      // console.log('1 = ', (this.emoc.Status_Common_Workflow__c === 'Monitoring') && moment(this.currentDate).isAfter(this.emoc.Show_Require_to_Extend__c, 'YYYY-MM-DD'))
      // console.log('2 =', this.emoc.Status_Common_Workflow__c)
      // console.log('3 this.currentDate =', this.currentDate, 'this.emoc.Show_Require_to_Extend__c', this.emoc.Show_Require_to_Extend__c, ' || ', moment(this.currentDate).isAfter(this.emoc.Show_Require_to_Extend__c, 'YYYY-MM-DD'), ' |22| ', moment(this.currentDate).isSameOrAfter(this.emoc.Show_Require_to_Extend__c, 'YYYY-MM-DD'))
      return (this.emoc.Status_Common_Workflow__c === 'Monitoring') && moment(this.currentDate).isSameOrAfter(this.emoc.Show_Require_to_Extend__c, 'YYYY-MM-DD')
    },
    showSectionReturnTemporaryMoc () { // มากกว่า 30
      if (this.emoc.Status_Common_Workflow__c === 'Monitoring') {
        return !(moment(this.currentDate).isSameOrAfter(this.emoc.Show_Require_to_Extend__c, 'YYYY-MM-DD'))
      } else {
        return ['Hazard Screening', 'Technical Review', 'APU Approval', 'VP Approval'].indexOf(this.emoc.Status_Common_Workflow__c) !== -1
      }
    },
    checkRequiredExtendTemporary () {
      return (this.showSectionExtendTemporary === true && this.emoc.Show_Correct_the_problem_section__c === true) && !(this.emoc.Extend_Require_to_extend__c || this.emoc.Not_Extend_Require_to_extend__c) && this.editEnable
    },
    showSectionRequireDemolish () {
      return this.emoc.Not_Extend_Require_to_extend__c === true
    },
    checkRequireDemolish () {
      return (this.showSectionRequireDemolish === true) && !(this.emoc.Yes_Require_to_demolish__c || this.emoc.No_Require_to_demolish__c) && this.editEnable
    },
    showSectionExtendPrevent () {
      return this.emoc.No_Require_to_demolish__c === true && this.emoc.flgShowConfirmeNoRequireDemolish === true
    },
    checkRequireExtendPreventoverdue () {
      return (this.showSectionExtendPrevent === true) && !(this.emocTemporaryHistory2.Yes2__c || this.emoc.Not_Extend_Require_to_extend_to_prevent__c) && this.editEnable
    },
    showSectionActivity () {
      return (['Monitoring'].indexOf(this.emoc.Status_Common_Workflow__c) === -1) && this.emoc.Yes_Monitoring_Program_Requirement__c === true
    },
    showSectionActivityAll () {
      return (this.emoc.Status_Common_Workflow__c === 'Monitoring') && this.emoc.Yes_Monitoring_Program_Requirement__c === true
    }
  },
  watch: {
    recId (newVal) {
      if (newVal !== '' && newVal) {
        this.numpage = 3
      }
    }
  },
  methods: {
    showPopup () {
      this.$refs.popupAfterSave.onOpen()
    },
    closePopup () {
      this.$refs.popupAfterSave.onClose()
    },
    getLabelFields (field) {
      let listFieldsLabel = {
        Yes_Monitoring_Program_Requirement__c: 'Monitoring Program Requirement',
        Start_Date_Monitoring_Program__c: 'Required Start date',
        End_Date_Monitoring_Program__c: 'Required End date',
        No_Monitoring_Program_Requirement__c: 'Monitoring Program Requirement',
        Remark_Monitoring_Program__c: 'Reason for not using monitoring program.',
        Yes__c: 'Correct the problem by using existing solution?',
        No__c: 'Correct the problem by using existing solution?',
        Yes_Require_to_demolish__c: 'Require to demolish/Return to normal?',
        No_Require_to_demolish__c: 'Require to demolish/Return to normal?',
        EMOC_Number_for_permanent_MOC__c: 'EMOC Number for permanent MOC',
        Notification_for_Permanent__c: 'Notification',
        Date__c: 'New Require End Date',
        Extend_Require_to_extend__c: 'Require to extend in this temporary MOC?',
        Not_Extend_Require_to_extend__c: 'Require to extend in this temporary MOC?',
        Yes2__c: 'Require to extend to prevent overdue during return to normal?',
        Not_Extend_Require_to_extend_to_prevent__c: 'Require to extend to prevent overdue during return to normal?'
      }
      if (field.indexOf('Name') !== -1) {
        return 'Activity'
      } else if (field.indexOf('Period_frequency__c') !== -1) {
        return 'Period/Frequency'
      } else if (field.indexOf('Action_by__c') !== -1) {
        return 'Action By'
      } else if (field.indexOf('Yes__c2') !== -1) {
        return 'Require to extend to prevent overdue during return to normal?'
      } else {
        return listFieldsLabel[field]
      }
    },
    focusField (target) {
      let elements = document.getElementsByName(target)
      let refMonitoringProgram = this.$refs.temporaryWorkflow.$refs.monitoringProgramCmp
      let activity
      let refCorrectProblem
      let refTemporary
      let refRequireDemolish
      let refRequireExtendPreventoverdue
      if (this.showSectionActivity) {
        activity = this.$refs.temporaryWorkflow.$refs.activityCmp
      }
      if (this.emocTemporaryHistory.showSectionCorrectProblem) {
        refCorrectProblem = this.$refs.temporaryWorkflow.$refs.correctProblemCmp
      }
      if (this.showSectionExtendTemporary) {
        refTemporary = this.$refs.temporaryWorkflow.$refs.extendTemporaryCmp
      }
      if (this.showSectionRequireDemolish) {
        refRequireDemolish = this.$refs.temporaryWorkflow.$refs.extendRequireDemolishCmp
      }
      if (this.showSectionExtendPrevent) {
        refRequireExtendPreventoverdue = this.$refs.temporaryWorkflow.$refs.extendRequireExtendPreventoverdueCmp
      }
      if ([
        'Yes_Monitoring_Program_Requirement__c',
        'Start_Date_Monitoring_Program__c',
        'End_Date_Monitoring_Program__c',
        'No_Monitoring_Program_Requirement__c',
        'Remark_Monitoring_Program__c'].indexOf(target) !== -1 && !refMonitoringProgram.$refs.expandMonitoringProgram.isOpen) {
        refMonitoringProgram.$refs.expandMonitoringProgram.toggle()
      } else if ([
        'Name',
        'Period_frequency__c',
        'Action_by__c'].indexOf(target) !== -1 && target.indexOf('Name') !== -1 && !activity.$refs.expandActivity.isOpen) {
        activity.$refs.expandActivity.toggle()
      } else if ([
        'Yes__c',
        'No__c'].indexOf(target) !== -1 && target.indexOf('Name') !== -1 && !refCorrectProblem.$refs.expandCorrectProblem.isOpen) {
        refCorrectProblem.$refs.expandCorrectProblem.toggle()
      } else if ([
        'Extend_Require_to_extend__c', 'Not_Extend_Require_to_extend__c'].indexOf(target) !== -1 && target.indexOf('Name') !== -1 && !refTemporary.$refs.expandExtendTemporary.isOpen) {
        refTemporary.$refs.expandExtendTemporary.toggle()
      } else if ([
        'Yes_Require_to_demolish__c',
        'No_Require_to_demolish__c',
        'EMOC_Number_for_permanent_MOC__c',
        'Notification_for_Permanent__c'].indexOf(target) !== -1 && target.indexOf('Name') !== -1 && !refRequireDemolish.$refs.expandRequireDemolish.isOpen) {
        refRequireDemolish.$refs.expandRequireDemolish.toggle()
      } else if ([
        'Yes2__c', 'Not_Extend_Require_to_extend_to_prevent__c', 'Remark__c'].indexOf(target) !== -1 && target.indexOf('Name') !== -1 && !refRequireExtendPreventoverdue.$refs.expandRequireExtendPreventoverdue.isOpen) {
        refRequireExtendPreventoverdue.$refs.expandRequireExtendPreventoverdue.toggle()
      }
      this.$nextTick(() => {
        elements[0].focus()
      })
    },
    clickSave () {
      Promise.all([
        this.$refs.temporaryWorkflow.$refs.monitoringProgramCmp.$validator.validateAll(),
        this.showSectionActivity ? this.$refs.temporaryWorkflow.$refs.activityCmp.$validator.validateAll() : true,
        this.emocTemporaryHistory.showSectionCorrectProblem ? this.$refs.temporaryWorkflow.$refs.correctProblemCmp.$validator.validateAll() : true,
        this.showSectionExtendTemporary ? this.$refs.temporaryWorkflow.$refs.extendTemporaryCmp.$validator.validateAll() : true,
        this.showSectionRequireDemolish ? this.$refs.temporaryWorkflow.$refs.extendRequireDemolishCmp.$validator.validateAll() : true,
        this.showSectionExtendPrevent ? this.$refs.temporaryWorkflow.$refs.extendRequireExtendPreventoverdueCmp.$validator.validateAll() : true,
        !this.checkRequiredMonitoringProgram,
        this.emocTemporaryHistory.showSectionCorrectProblem ? !this.checkRequiredCorrectProblem : true,
        this.showSectionExtendTemporary ? !this.checkRequiredExtendTemporary : true,
        this.showSectionRequireDemolish ? !this.checkRequireDemolish : true,
        this.showSectionExtendPrevent ? !this.checkRequireExtendPreventoverdue : true
      ]).then((result) => {
        if (result.filter(df => !df).length === 0) {
          this.showPopupLoader()
          let Enddate = (this.emoc.newEndDateTempoHis !== '' && (this.emoc.Extend_Require_to_extend__c || this.emocTemporaryHistory2.Yes2__c)) ? this.emoc.newEndDateTempoHis : this.emoc.End_date__c
          let emocApp = {
            Id: this.emoc.Id,
            Yes_Monitoring_Program_Requirement__c: this.emoc.Yes_Monitoring_Program_Requirement__c,
            No_Monitoring_Program_Requirement__c: this.emoc.No_Monitoring_Program_Requirement__c,
            Start_Date_Monitoring_Program__c: (this.emoc.Yes_Monitoring_Program_Requirement__c === true && this.emoc.Start_Date_Monitoring_Program__c !== '') ? this.parseDate(this.emoc.Start_Date_Monitoring_Program__c) : null,
            End_Date_Monitoring_Program__c: (this.emoc.Yes_Monitoring_Program_Requirement__c === true && Enddate !== '') ? this.parseDate(Enddate) : null,
            Remark_Monitoring_Program__c: this.emoc.No_Monitoring_Program_Requirement__c === true ? this.emoc.Remark_Monitoring_Program__c : '',
            Show_Require_to_Extend__c: (this.emoc.Yes_Monitoring_Program_Requirement__c === true && this.emoc.Show_Require_to_Extend__c !== '') ? this.emoc.Show_Require_to_Extend__c : null,
            Extend_Require_to_extend__c: this.emoc.Extend_Require_to_extend__c,
            Not_Extend_Require_to_extend__c: this.emoc.Not_Extend_Require_to_extend__c,
            Reason_for_Extension_temporary_MOC__c: this.emoc.Not_Extend_Require_to_extend__c === true ? this.emoc.Reason_for_Extension_temporary_MOC__c : '',
            Yes_Require_to_demolish__c: this.emoc.Yes_Require_to_demolish__c,
            No_Require_to_demolish__c: this.emoc.No_Require_to_demolish__c,
            Reason_for_Extension_return_to_normal__c: this.emoc.Reason_for_Extension_return_to_normal__c,
            EMOC_Number_for_permanent_MOC__c: this.emoc.EMOC_Number_for_permanent_MOC__c,
            Notification_for_Permanent__c: this.emoc.Notification_for_Permanent__c,
            End_date__c: Enddate !== '' ? this.parseDate(Enddate) : null,
            Return_to_Normal__c: this.emoc.Return_to_Normal__c,
            Action_by_return_to_normal__c: this.emoc.Action_by_return_to_normal__c,
            Not_Extend_Require_to_extend_to_prevent__c: this.emoc.Not_Extend_Require_to_extend_to_prevent__c,
            Remark__c: this.emoc.Remark__c,
            Extend_Require_to_extend_to_prevent__c: this.emocTemporaryHistory2.Yes2__c
          }
          let emocActivity = this.$store.state.emocActivity.filter(df => {
            return df.Name !== ''
          }).map(dm => {
            return {
              Id: (dm.Id !== '') ? dm.Id : null,
              flgDelete: dm.flgDelete,
              E_MOC_Application__c: dm.E_MOC_Application__c,
              Name: dm.Name,
              Period_frequency__c: dm.Period_frequency__c,
              Action_by__c: dm.Action_by__c,
              Frequency_days__c: dm.Frequency_days__c,
              Acknowledge__c: dm.Acknowledge__c,
              Acknowledge_by__c: dm.Acknowledge_by__c
            }
          })
          let emocTemporaryHistory = { // emocTemporaryHistory
            Id: (this.emocTemporaryHistory.Id !== '') ? this.emocTemporaryHistory.Id : null,
            E_MOC_Application__c: this.emoc.Id,
            Yes__c: this.emocTemporaryHistory.Yes__c,
            No__c: this.emocTemporaryHistory.No__c,
            Remark__c: this.emocTemporaryHistory.Remark__c,
            Type__c: 'Correct the problem by using existing solution',
            Date__c: (this.emocTemporaryHistory.Yes__c === true && this.emocTemporaryHistory.Date__c !== '') ? this.parseDate(this.emocTemporaryHistory.Date__c) : null
          }
          let emocTemporaryHistory2 = { // emocTemporaryHistory || Require to extend to prevent overdue during return to normal?
            Id: (this.emocTemporaryHistory2.Id !== '') ? this.emocTemporaryHistory2.Id : null,
            Action_by__c: this.emoc.User_Login,
            E_MOC_Application__c: this.emoc.Id,
            Yes__c: this.emoc.Comfirm && this.emocTemporaryHistory2.Yes2__c,
            Type__c: 'Require to extend to prevent overdue during return to normal',
            Date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.End_date__Old) : null,
            Old_require_end_date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.End_date__Old) : null,
            End_Date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.newEndDateTempoHis) : null,
            New_require_end_date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.newEndDateTempoHis) : null
          }
          let emocTemporaryHistory3 = { // emocTemporaryHistory || Require to extend in this temporary MOC?
            Id: null,
            Action_by__c: this.emoc.User_Login,
            E_MOC_Application__c: this.emoc.Id,
            Yes__c: this.emoc.Comfirm && this.emoc.Extend_Require_to_extend__c,
            Type__c: 'Require to extend in this temporary MOC?',
            Date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.End_date__Old) : null,
            Old_require_end_date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.End_date__Old) : null,
            End_Date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.newEndDateTempoHis) : null,
            New_require_end_date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.newEndDateTempoHis) : null
          }
          let paramSave = {
            emocApp: emocApp,
            emocActivity: emocActivity,
            emocTemporaryHistory: emocTemporaryHistory,
            emocTemporaryHistory2: emocTemporaryHistory2,
            emocTemporaryHistory3: emocTemporaryHistory3
          }
          console.log('paramSave', paramSave)
          this.$store.dispatch('saveCreateEmoc', JSON.stringify(paramSave))
            .then(result => {
              this.closePopupLoader()
              if (result.statusSuccess) {
                this.showPopup()
                this.resultSuccess = result
              }
            })
          this.hasError = false
        } else {
          this.errorBagItems = Object.assign([], this.errors.items)
          if (this.checkRequiredMonitoringProgram) {
            this.errorBagItems.push({field: 'Yes_Monitoring_Program_Requirement__c'})
          }
          if (this.checkRequiredCorrectProblem) {
            this.errorBagItems.push({field: 'Yes__c'})
          }
          if (this.checkRequiredExtendTemporary) {
            this.errorBagItems.push({field: 'Extend_Require_to_extend__c'})
          }
          if (this.checkRequireDemolish) {
            this.errorBagItems.push({field: 'Yes_Require_to_demolish__c'})
          }
          if (this.checkRequireExtendPreventoverdue) {
            this.errorBagItems.push({field: 'Yes2__c'})
          }
          this.hasError = true
          this.$refs.popoverErrorStd.closePopover()
        }
      })
    },
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    showPopupLoader () {
      this.$refs.popupLoader.onOpen()
    },
    closePopupLoader () {
      this.$refs.popupLoader.onClose()
    },
    clickEdit () {
      this.showPopupLoader()
      setTimeout(() => {
        this.closePopupLoader()
        this.$nextTick(() => {
          this.$store.commit('setEditEnable', true)
        })
      }, 500)
    }
  },
  components: {
    temporaryWorkflow
  }
}
</script>

<style src="../assets/style.css"></style>


