<template>
  <div name="correct-problem">
    <expandable-section-std title="Correct the problem by using existing solution?" ref="expandCorrectProblem" :closedOnStart="checkStatusIsFold">
      <div class="data-frame">
        <div class="row">
          <div class="col-6">
            <checkbox-std
              v-model="emocTemporaryHistory.Yes__c"
              label="Yes"
              :inline="true"
              :disabled="!editEnable || correctProblemDisable || emoc.Show_Correct_the_problem_section__c"
            />
          </div>
          <div class="col-6">
            <checkbox-std
              v-model="emocTemporaryHistory.No__c"
              label="No"
              :inline="true"
              :disabled="!editEnable || correctProblemDisable || emoc.Show_Correct_the_problem_section__c"
            />
          </div>
        </div>
      </div>
      <div v-show="checkRequiredCorrectProblem" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          {{'The correct the problem by using existing solution field is required.'}}
        </div>
      </div>
    </expandable-section-std>

    <modal-std size="large" ref="refModalCorrectProblem" @close="afterClosePopup" id="correctProblem">
      <template #header>
        <div v-if="emocTemporaryHistory.Yes__c" style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">Correct the problem by using existing solution</span></div>
        <div v-else style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">It cannot be corrected by using existing solution</span></div>
      </template>
      <template #content>
        <correct-problem-popup  :emocTemporaryHistory="emocTemporaryHistory"
                                :labelTypeCheckbox="labelTypeCheckbox"
                                ref="refCorrectProblem"/>
      </template>
      <template #footer>
        <div style="text-align: right;">
          <button class="slds-button slds-button_neutral" @click="callBackCorrectProblem">Cancel</button>
          <button v-if="btnComfirmDisable" class="slds-button slds-button_brand" @click="callSave">Comfirm</button>
          <button v-else class="slds-button slds-button_brand" disabled @click="callSave">Comfirm</button>
        </div>
      </template>
    </modal-std>

    <modal-after-save id="afterSaveCorrectProblem"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveCorrectProblem"/>
  </div>
</template>
<script>
import correctProblemPopup from './CorrectProblemPopup.vue'

export default {
  name: 'correct-problem',
  props: [
    'emoc',
    'recId',
    'checkRequiredCorrectProblem',
    'disabledNextStage',
    'editEnable',
    'clickSaveMain',
    'checkStatusIsFold'
  ],
  data () {
    return {
      labelTypeCheckbox: ''
    }
  },
  computed: {
    btnComfirmDisable () { return this.$store.state.btnComfirmDisable },
    emocTemporaryHistory () { return this.$store.state.emocTemporaryHistory },
    correctProblemDisable () { return this.$store.state.correctProblemDisable }
  },
  watch: {
    'emocTemporaryHistory.No__c' (newValue) {
      if (newValue) {
        this.emocTemporaryHistory.Yes__c = false
        if (newValue === true) {
          this.showPopupCorrectProblem()
          this.labelTypeCheckbox = 'No'
        }
      }
    },
    'emocTemporaryHistory.Yes__c' (newValue) {
      if (newValue) {
        this.emocTemporaryHistory.No__c = false
        if (newValue === true) {
          this.showPopupCorrectProblem()
          this.labelTypeCheckbox = 'Yes'
        }
      }
    }
  },
  methods: {
    showPopupCorrectProblem () {
      this.$refs.refModalCorrectProblem.onOpen()
    },
    afterClosePopup () {
    },
    callBackCorrectProblem () {
      this.$nextTick(() => {
        this.$refs.refModalCorrectProblem.onClose()
      })
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSaveCorrectProblem.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    callSave () {
      this.emocTemporaryHistory.Type__c = 'Correct the problem by using existing solution'
      this.callBackCorrectProblem()
      this.$store.commit('setCorrectProblemDisable', true)
      this.clickSaveMain()
    }
  },
  components: {
    correctProblemPopup
  }
}
</script>


